<script>
  function tableSnippetRange(sortedTeams, fullTeamName) {
    let teamStandingsIdx = sortedTeams.indexOf(fullTeamName);

    let low = teamStandingsIdx - 3;
    let high = teamStandingsIdx + 4;
    if (low < 0) {
      let overflow = low;
      high -= overflow;
      low = 0;
    }
    if (high > sortedTeams.length - 1) {
      let overflow = high - sortedTeams.length;
      low -= overflow;
      high = sortedTeams.length;
    }

    return [low, high];
  }

  function buildTableSnippet() {
    let sortedTeams = Object.keys(data.standings).sort(function (teamA, teamB) {
      return (
        data.standings[teamA][data._id].position -
        data.standings[teamB][data._id].position
      );
    });

    let [low, high] = tableSnippetRange(sortedTeams, fullTeamName);

    let teamTableIdx;
    let rows = [];
    for (let i = low; i < high; i++) {
      if (sortedTeams[i] == fullTeamName) {
        teamTableIdx = i - low;
      }
      rows.push({
        name: sortedTeams[i],
        position: data.standings[sortedTeams[i]][data._id].position,
        points: data.standings[sortedTeams[i]][data._id].points,
        gd: data.standings[sortedTeams[i]][data._id].gD,
      });
    }

    tableSnippet = {
      teamTableIdx: teamTableIdx,
      rows: rows,
    };
  }

  let tableSnippet;
  $: fullTeamName && buildTableSnippet()

  export let data, team, fullTeamName, getAlias;
</script>

<div class="table-snippet">
  {#if tableSnippet != undefined}
    <div class="divider" />
    <div class="table-row">
      <div class="table-element table-position column-title" />
      <div class="table-element table-team-name column-title">Team</div>
      <div class="table-element table-gd column-title">GD</div>
      <div class="table-element table-points column-title">Points</div>
    </div>

    {#each tableSnippet.rows as row, i}
      <!-- Divider -->
      {#if i == 0}
        {#if i != tableSnippet.teamTableIdx}
          <div id="divider" />
        {/if}
      {:else if i - 1 != tableSnippet.teamTableIdx && i != tableSnippet.teamTableIdx}
        <div id="divider" />
      {/if}
      <!-- Row of table -->
      {#if i == tableSnippet.teamTableIdx}
        <!-- Highlighted row for the team of the current page -->
        <div
          class="table-row this-team"
          style="background-color: var(--{team});"
        >
          <div
            class="table-element table-position this-team"
            style="color: var(--{team}-secondary);"
          >
            {row.position}
          </div>
          <a
            href="/{team}"
            class="table-element table-team-name this-team"
            style="color: var(--{team}-secondary);"
          >
            {getAlias(row.name)}
          </a>
          <div
            class="table-element table-gd this-team"
            style="color: var(--{team}-secondary);"
          >
            {row.gd}
          </div>
          <div
            class="table-element table-points this-team"
            style="color: var(--{team}-secondary);"
          >
            {row.points}
          </div>
        </div>
      {:else}
        <!-- Plain row -->
        <div class="table-row">
          <div class="table-element table-position">
            {row.position}
          </div>
          <a
            href="/{row.name.toLowerCase().replace(/ /g, '-')}"
            class="table-element table-team-name"
          >
            {getAlias(row.name)}
          </a>
          <div class="table-element table-gd">
            {row.gd}
          </div>
          <div class="table-element table-points">
            {row.points}
          </div>
        </div>
      {/if}
    {/each}
    {#if tableSnippet.teamTableIdx != 6}
      <div id="divider" />
    {/if}
  {/if}
</div>

<style scoped>
  .table-snippet {
    position: relative;
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    width: 100%;
    height: auto;
  }

  .table-row {
    display: flex;
    padding: 5px 5%;
    border-radius: var(--border-radius);
  }

  .table-row.this-team {
    padding: 13px 5%;
    font-size: 20px;
  }

  .this-team {
    font-size: 1.1em !important;
  }

  #divider {
    align-self: center;
    border-bottom: 1px solid grey;
    width: 90%;
    margin: auto;
  }

  .column-title {
    font-weight: 700;
  }

  .table-position {
    width: 7%;
  }

  .table-team-name {
    width: 63%;
    text-align: left;
    margin-left: 8px;
    color: #333333;
  }

  .table-gd {
    width: 15%;
  }

  .table-points {
    width: 15%;
  }
</style>
